<template>
  <div class="content-wrapper">
    <div class="loading-container" v-if="isLoading">
      <div class="loading-wrapper">
        <me-loading></me-loading>
      </div>
    </div>
    <me-scroll ref="scroll" @scroll-end="scrollEnd">
      <div class="content">
        <div class="pic" v-if="content.banner">
          <a :href="content.banner.linkUrl" class="pic-link">
            <img @load="updateScroll" :src="content.banner.picUrl" alt="" class="pic-img">
          </a>
        </div>
        <div
          class="section"
          v-for="(section, index) in content.data"
          :key="index"
        >
          <h4 class="section-title">{{section.name}}</h4>
          <ul class="section-list">
            <li
              class="section-item"
              v-for="(item, i) in section.itemList"
             :key="i"
            >
              <a href="" class="section-link">
                <p class="section-pic" v-if="item.picUrl">
                  <img v-lazy="item.picUrl" alt="" class="section-img" />
                </p>
                <p class="section-name">{{item.name}}</p>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </me-scroll>

    <div class="g-backtop-container">
      <me-backtop @backtop="backToTop" :visible="isBacktopVisible"></me-backtop>
    </div>
  </div>
</template>

<script>
import MeScroll from 'base/scroll';
import MeLoading from 'base/loading'
import MeBacktop from 'base/backtop';
import {getCategoryContent} from 'api/category';
import storage from 'assets/js/storage';
import {CATEGORY_CONTENT_KEY,CATEGORY_CONTENT_UPDATE_TIME_INTERVAL} from './config';


export default {
  name:'CategoryContent',
  components:{
    MeScroll,
    MeLoading,
    MeBacktop
  },
  //获取效验数据
  props:{
    curId:{
      type:String,
      default:''
    }
  },
  data(){
    return{
      content:{},
      isBacktopVisible:false,
      isLoading:false
    };
  },
  //监听数据
  watch:{
    curId(id){
      this.isLoading = true;
      this.getContent(id).then(() => {
        this.isLoading = false;
        this.backToTop(0);
      })
    }
  },
  //定义事件函数的对象
  methods:{
    getContent(id){
      let contents = storage.get(CATEGORY_CONTENT_KEY);//通过键值对   获取到本地内容
      let updateTime;//声明变量保存上一次更新时间
      const curTime = new Date().getTime();//定义当前时间  获取时间戳

      //这是contents能够获取内容的时候
      if (contents && contents[id]){ //判断contents 和 它下面的id 有没有相应的内容   是通过id保存的数据
        updateTime = contents[id].updateTime || 0;//先获取一下id对应的updateTime 如果没有为0
        //判断当前时间减去updateTime如果比传进来的常量CATEGORY_CONTENT_UPDATE_TIME_INTERVAL时间间隔要短的话这就表示要走缓存
        if (curTime - updateTime <= CATEGORY_CONTENT_UPDATE_TIME_INTERVAL) {
          return this.getContentByLocalStorage(contents[id]);  //return返回是希望getContent可以返回一个Promise对象 这样在数据成功获取到之后还可以操作
        } else {//走HTTP
          return this.getContentByHTTP(id).then(() => {//成功获取之后更新一下   更新写在成功之后
            this.updateLocalStorage(contents, id, curTime);//更新的方法  缓存
          })
        };
      } else {//获取不到内容的时候完全走HTTP
        return this.getContentByHTTP(id).then(() => {
            this.updateLocalStorage(contents, id, curTime);
        });
      }
    },
    getContentByLocalStorage(content){
      this.content = content.data;
      return Promise.resolve();//返回Promise对象  成功获取到数据
    },
    getContentByHTTP(id) {
      return getCategoryContent(id).then(data => {
        return new Promise(resolve => {//只有你成功获取到了数据之后才会执行resolve   如果没有获取就不会走getContentByHTTP的then
          if (data) {
            this.content = data;
            resolve();
          }
        });
      });
    },
    updateLocalStorage(contents, id, curTime){//更新缓存
      contents = contents || {};//判断有没有值 没有给一个空对象
      contents[id] = {};//对应的id直接变成空对象
      contents[id].data = this.content;//数据就是当前的内容
      contents[id].updateTime = curTime;//上次更新的时间就是目前更新的
      storage.set(CATEGORY_CONTENT_KEY,contents);//设置进去覆盖原来的
    },

    //点击回到顶部
    backToTop(speed){
      this.$refs.scroll && this.$refs.scroll.scrollToTop(speed);
    },
    
    updateScroll(){
      this.$refs.scroll && this.$refs.scroll.update();
    },
    //根据滚动长度显示回到顶部
    scrollEnd(translate, scroll, pulling){
      this.isBacktopVisible = translate < 0 && -translate > scroll.height;
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~assets/scss/mixins';

  .content-wrapper {
    position: relative;
    height: 100%;
  }

  .loading-container {
    position: absolute;
    top: 0;
    left: 0;
    z-index: $category-popup-z-index;
    @include flex-center();
    width: 100%;
    height: 100%;
    /*background-color: $modal-bgc;*/

    .mine-loading {
      color: #fff;
      font-size: 14px;
    }
  }
  .loading-wrapper {
    width: 50%;
    padding: 30px 0;
    background-color: $modal-bgc;
    border-radius: 4px;
  }

  .content {
    padding: 10px;
  }

  .pic {
    margin-bottom: 12px;

    &-link {
      display: block;
    }

    &-img {
      width: 100%;
    }
  }

  .section {
    margin-bottom: 12px;

    &:last-child {
      margin-bottom: 0;
    }

    &-title {
      height: 28px;
      line-height: 28px;
      color: #080808;
      font-weight: bold;
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      background-color: #fff;
      padding: 10px 10px 0;
    }

    &-item {
      width: (100% / 3);
    }

    &-link {
      display: block;
    }

    &-pic {
      position: relative;
      width: 80%;
      padding-bottom: 80%;
      margin: 0 auto;
    }

    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &-name {
      height: 36px;
      line-height: 36px;
      text-align: center;
      @include ellipsis();
    }
  }
  .g-backtop-container {
    bottom: 10px;
  }
</style>



