<template>
  <transition name="mine-backtop">
    <a href="javascript:;"
    class="mine-backtop"
    v-show="visible"
    @click="backToTop"
    >
      <i class="iconfont iconfanhuidingbu"></i>
    </a>
  </transition>
</template>

<script>
export default {
  mane:'MeBacktop',
  props:{
    visible:{
      type:Boolean,
      default:false
    }
  },
  methods:{
    backToTop(){
      this.$emit('backtop');
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '~assets/scss/mixins';

  .mine-backtop{
    overflow: hidden;
    @include flex-center();
    width: 45px;
    height: 45px;
    background-color: rgba(0,0,0,0.6);
    border: none;
    border-radius: 50%;

    .iconfont{
      color: #fff;
      font-size: 38px;
    }
  }

  .mine-backtop{
    &-leave-active,
    &-enter-active{
      transition: opacity 0.4s;
    }
    &-leave-to,
    &-enter{
      opacity: 0;
    }
    &-leave,
    &-enter-to{
      opacity: 1;
    }
    
  }
</style>



