// 先看一看客户端有没有相应的数据如果没有 在从服务端获取
// 如果有的话 先看一下上次更新的时间
// 设置一个更新周期一天  现在的时间减去上次更新的时间  和这个更新周期一天来比较
// 如果不到一天说明缓存还没有失效  这样直接从客户端获取内容
// 如果超过一天说明缓存已经失效就需要从 服务端获取数据
// 然后更新一下缓存
// 把内容放到缓存中离线的时候也可以看到  不可以太多内容

// 这里使用的客户端缓存Local storage

const storage = window.localStorage;
// 使用 window.localStorage 创建一个本地存储的键值对
// 保存数据语法：localStorage.setItem("key", "value");
// 读取数据语法：var lastname = localStorage.getItem("key");
// 删除数据语法：localStorage.removeItem("key");
// 擦除所有本地存储：localStorage.clear()

// 直接返回一些API
export default {
  // 序列化   js对象转JSON字符
  set(key, val) {
    if (val === undefined) {
      return;
    }
    storage.setItem(key, serialize(val));
  },
  get(key, def) {
    // 反序列化  JSON字符转js对象
    const val = deserialize(storage.getItem(key));
    return val === undefined ? def : val;
  },
  remove(key) {
    storage.removeItem(key);
  },
  clear() {
    storage.clear();
  }
};

function serialize(val) {
  return JSON.stringify(val); // JSON.stringify()将json对象 转换成json字符串。
}

function deserialize(val) {
  if (typeof val !== 'string') {
    return undefined;
  }
  try {
    return JSON.parse(val); // JSON.parse() 方法用于将一个 JSON 字符串转换为 对象
  } catch (e) {
    return val || undefined;
  }
}

